<template>
  <div class="manage" id="articleList">
    <div class="manage-title">
      <p>Article管理</p>
      <div>
        <button>保存排序</button>
        <button @click="add">新增</button>
      </div>
    </div>
    <table class="manage-content">
      <thead class="manage-content-title">
      <tr>
        <th>序号</th>
        <th>图片</th>
        <th>标题</th>
        <th>类型</th>
        <th>创建者</th>
        <th>创建时间</th>
        <th>修改时间</th>
        <th>状态</th>
        <th>操作</th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(x, index) in articleList">
        <td>{{index+1}}</td>
        <td><img :src="x.img"></td>
        <td>{{x.title}}</td>
        <td>{{x.type | types}}</td>
        <td>{{x.author}}</td>
        <td>
          <div>{{x.createAt | dateformat('YYYY-MM-DD')}}</div>
          <div>{{x.createAt | dateformat('HH:mm:ss')}}</div>
        </td>
        <td>
          <div>{{x.updateAt | dateformat('YYYY-MM-DD')}}</div>
          <div>{{x.updateAt | dateformat('HH:mm:ss')}}</div>
        </td>
        <td>{{x.status | status}}</td>
        <td>
          <button @click="onOrOffLine(x.id, x.status)">{{x.status | down}}</button>
          <button @click="edit(x.id)">编辑</button>
          <button @click="deleteId(x.id)">删除</button>
        </td>
      </tr>
      </tbody>
    </table>
  </div>

</template>

<script>
  import Bus from '@/assets/api/bus.js'
  export default {
    inject: ['reload'],
    name: 'ArticleList',
    data () {
      return {
        id: '',
        onOff: '',
        articleList: [],
        article: {
          title: this.$route.query.title,
          author: this.$route.query.author,
          startAt: this.$route.query.startAt,
          endAt: this.$route.query.endAt,
          type: this.$route.query.type,
          status: this.$route.query.status,
          page: this.$route.query.page,
          size: this.$route.query.size,
        }
      }
    },
    // props:{
    //   articleList:{
    //     type:Array,
    //     require:true
    //   }
    // },
    methods: {
      // 上下线
      onOrOffLine(id,onOff) {
        this.id = id
        console.log(id);
        console.log(onOff);
        if (onOff == 1){
          this.onOff = 2
        }
        else {
          this.onOff = 1
        }
        console.log(this.id);
        console.log(this.onOff);
        this.$confirm('是否确认操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios({
            method: 'put',
            url: '/api/a/u/article/status',
            params: {
              id: this.id,
              status: this.onOff
            }
          })
            .then(() => this.refresh()) //刷新组件，重新请求数据
          this.$message({
            type: 'success',
            message: '操作成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消操作'
          });
        });
      },
      // 删除
      deleteId(id) {
        console.log(this.$route.query);
        this.id = id
        this.$confirm('是否确认删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$axios({
            method: 'delete',
            url: '/api/a/u/article/'+this.id,
            params: {
              id: this.id,
            }
          })
            .then(() => this.refresh())  //刷新组件，重新请求数据

          this.$message({
            type: 'success',
            message: '操作成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消操作'
          });
        });
      },
      // 编辑
      edit(id){
        this.$router.push({
          name:'add',
          query:{
            id: id,
          }
        })
      },
      // 新增
      add(){
        this.$router.push({
          name:'add',
          query:{
          }
        })
      },
      // 请求和上下线后刷新数据
      refresh(){
        this.$axios({
          method:'get',
          url: '/api/a/article/search/',
          params: this.article
        })
          .then((res) => {
            this.articleList = res.data.data.articleList
            console.log(res.data.data);
            Bus.$emit('pageInfo',res.data.data.total)
          })
      }
    },
    // 载入时请求数据
     created:function () {
      this.refresh()
     },
    watch:{
      $route(){
        this.reload()
      }
    }
  }
</script>

<style scoped>

  .list-box .manage {
    margin-top: 10px;
    border: 1px solid #cccccc; }
  .list-box .manage .manage-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 10px;
    border-bottom: 1px solid #cccccc; }
  .list-box .manage > .manage-content {
    margin: 10px 10px;
    width: calc(100% - 20px); }
  .list-box .manage > .manage-content > thead {
    background-color: #f2f2f2; }
  .list-box .manage > .manage-content > thead > tr {
    border-collapse: collapse;
    border: 1px solid #cccccc; }
  .list-box .manage > .manage-content > thead > tr > th {
    padding: 8px 0;
    text-align: center;
    font-weight: bold; }
  .list-box .manage > .manage-content > tbody > tr {
    border: 1px solid #cccccc;
    border-collapse: collapse; }
  .list-box .manage > .manage-content > tbody > tr > td {
    padding: 10px 0;
    text-align: center;
    vertical-align: middle; }
  .list-box .manage > .manage-content > tbody > tr > td > img {
    height: 80px;
    width: 120px; }
</style>
