<template>
  <div class="bottom">
    <div class="quantity col-3">
      每页显示
      <el-input size="small" v-model="setSize" @input="inputLimit"></el-input>
      条
    </div>
    <div class="col-5">
      <div class="block">
        <el-pagination
          layout="prev, pager, next"
          :total="total"
          :current-page="currentPage"
          @prev-click="prePage"
          @next-click="nextPage"
          @current-change="change"
        >
        </el-pagination>
      </div>
    </div>
    <div class="jump col-3">
      跳转至
      <el-input size="small" v-model="jumpPage" @input="inputLimit"></el-input>
      页
    </div>
    <button class="col-1" @click="sizeAndPage">确认</button>
  </div>

</template>

<script>
  import Bus from '@/assets/api/bus.js'

  export default {
      name: 'PageChange',
    data(){
      return{
        article: {
          title: this.$route.query.title,
          author: this.$route.query.author,
          startAt: this.$route.query.startAt,
          endAt: this.$route.query.endAt,
          type: this.$route.query.type,
          status: this.$route.query.status,
          page: this.$route.query.page,
          size: this.$route.query.size,
        },
        currentPage: Number(this.$route.query.page),
        jumpPage: '',
        setSize:'',
        total: 0
      }
    },
    methods:{
        inputLimit(){
          this.jumpPage = this.jumpPage.replace(/\D/g,'')
          this.setSize = this.setSize.replace(/\D/g,'')
        },
      push(){
        this.$router.push({
          name:'Article',
          query: this.article
        })
      },
      nextPage(){
        console.log(this.$route.query);
        this.article.page ++
        this.push()
      },
      prePage(){
        this.article.page --
        this.push()
      },
      change(val){
        this.article.page = val
        this.push()
      },
      sizeAndPage(){
        this.article.page = this.jumpPage
        this.article.size = this.setSize
        this.push()
      },
    },
    mounted () {
        Bus.$on('pageInfo',(data) => {
          this.total = data
        })
    }
  }
</script>

